.container {
  width: 100%;
  max-width: 1246px;
  min-width: 640px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
}
.container--space-arround {
  justify-content: space-around;
}
.container--space-between {
  justify-content: space-between;
}
.container--column {
  flex-direction: column;
}
.top-header {
  background-color: #cb1717;
}
.cities {
  position: relative;
}
.cities__dropdown {
  font-family: "AvenirNextBold";
  font-size: 16px;
  color: #fe9389;
  text-decoration: none;
  line-height: 40px;
  -webkit-transition: color 0.4s ease;
  -moz-transition: color 0.4s ease;
  -o-transition: color 0.4s ease;
  transition: color 0.4s ease;
}
.cities__dropdown:hover {
  color: #fff;
}
.cities__dropdown:before,
.cities__dropdown:after {
  content: '';
  display: inline-block;
}
.cities__dropdown:before {
  background-image: url(../img/sprite/png-sprite.png);
  background-position: -22px 0px;
  width: 12px;
  height: 16px;
  margin-right: 8px;
}
.cities__dropdown:after {
  background-image: url(../img/sprite/png-sprite.png);
  background-position: 0px -21px;
  width: 12px;
  height: 7px;
  margin-left: 8px;
}
.cities__dropdown-list {
  -webkit-transition: opacity 0.5s linear;
  -moz-transition: opacity 0.5s linear;
  -o-transition: opacity 0.5s linear;
  transition: opacity 0.5s linear;
  opacity: 0;
  list-style: none;
  position: absolute;
  background-color: #cb1717;
  margin: 0;
  padding: 0 20px;
  padding-bottom: 10px;
}
.cities__dropdown-list--open {
  opacity: 1;
}
.cities__dropdown-link {
  font-size: 16px;
  color: #fe9389;
  text-decoration: none;
  line-height: 40px;
  white-space: nowrap;
  -webkit-transition: color 0.4s ease;
  -moz-transition: color 0.4s ease;
  -o-transition: color 0.4s ease;
  transition: color 0.4s ease;
}
.cities__dropdown-link:hover {
  color: #fff;
}
.top-menu {
  padding-left: 0;
  margin: 0;
  flex-grow: 2;
}
.top-menu__list {
  margin: 0;
  padding: 0;
  text-align: center;
}
.top-menu__item {
  display: inline-block;
}
.top-menu__link {
  -webkit-transition: color 0.4s ease;
  -moz-transition: color 0.4s ease;
  -o-transition: color 0.4s ease;
  transition: color 0.4s ease;
  font-size: 16px;
  color: #fe9389;
  text-decoration: none;
  line-height: 40px;
  display: inline-block;
  padding: 0 18px;
}
.top-menu__link:hover {
  color: #fff;
}
.top-phone {
  font-size: 18px;
  color: #fe9389;
  text-decoration: none;
  line-height: 40px;
  display: inline-block;
  -webkit-transition: color 0.4s ease;
  -moz-transition: color 0.4s ease;
  -o-transition: color 0.4s ease;
  transition: color 0.4s ease;
}
.top-phone:hover {
  color: #fff;
}
.header {
  background-color: #ec2d2d;
}
.header__logo-img {
  width: 165px;
  height: 40px;
  padding: 20px 0;
}
.header-menu {
  padding-left: 0;
  margin: 0;
  flex-grow: 2;
  margin-left: 108px;
}
.header-menu__list {
  margin: 0;
  padding: 0;
  text-align: left;
}
.header-menu__item {
  display: inline-block;
  padding: 0 25px;
}
.header-menu__item:first-child {
  padding-left: 0;
}
.header-menu__link {
  -webkit-transition: color 0.4s ease;
  -moz-transition: color 0.4s ease;
  -o-transition: color 0.4s ease;
  transition: color 0.4s ease;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
  line-height: 80px;
  display: inline-block;
}
.header-menu__link:hover {
  color: #fe9389;
}
.header-search {
  display: inline-block;
  background-image: url(../img/sprite/png-sprite.png);
  background-position: 0px 0px;
  width: 21px;
  height: 20px;
  margin: 30px 0;
  margin-right: 35px;
}
.header-login {
  -webkit-transition: background-color 0.4s ease;
  -moz-transition: background-color 0.4s ease;
  -o-transition: background-color 0.4s ease;
  transition: background-color 0.4s ease;
  box-sizing: border-box;
  display: inline-block;
  font-size: 18px;
  color: #fff;
  text-decoration: none;
  line-height: 40px;
  height: 40px;
  padding: 0 20px;
  border: 2px solid #fff;
  border-radius: 25px;
  margin: 20px 0;
}
.header-login:hover {
  background-color: #fff;
  color: #ec2d2d;
}
