/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/
@sprite-cities-arrow-name: 'sprite__cities-arrow';
@sprite-cities-arrow-x: 0px;
@sprite-cities-arrow-y: 21px;
@sprite-cities-arrow-offset-x: 0px;
@sprite-cities-arrow-offset-y: -21px;
@sprite-cities-arrow-width: 12px;
@sprite-cities-arrow-height: 7px;
@sprite-cities-arrow-total-width: 34px;
@sprite-cities-arrow-total-height: 28px;
@sprite-cities-arrow-image: '../img/sprite/png-sprite.png';
@sprite-cities-arrow: 0px 21px 0px -21px 12px 7px 34px 28px '../img/sprite/png-sprite.png' 'sprite__cities-arrow';
@sprite-cities-marker-name: 'sprite__cities-marker';
@sprite-cities-marker-x: 22px;
@sprite-cities-marker-y: 0px;
@sprite-cities-marker-offset-x: -22px;
@sprite-cities-marker-offset-y: 0px;
@sprite-cities-marker-width: 12px;
@sprite-cities-marker-height: 16px;
@sprite-cities-marker-total-width: 34px;
@sprite-cities-marker-total-height: 28px;
@sprite-cities-marker-image: '../img/sprite/png-sprite.png';
@sprite-cities-marker: 22px 0px -22px 0px 12px 16px 34px 28px '../img/sprite/png-sprite.png' 'sprite__cities-marker';
@sprite-search-name: 'sprite__search';
@sprite-search-x: 0px;
@sprite-search-y: 0px;
@sprite-search-offset-x: 0px;
@sprite-search-offset-y: 0px;
@sprite-search-width: 21px;
@sprite-search-height: 20px;
@sprite-search-total-width: 34px;
@sprite-search-total-height: 28px;
@sprite-search-image: '../img/sprite/png-sprite.png';
@sprite-search: 0px 0px 0px 0px 21px 20px 34px 28px '../img/sprite/png-sprite.png' 'sprite__search';
@spritesheet-width: 34px;
@spritesheet-height: 28px;
@spritesheet-image: '../img/sprite/png-sprite.png';
@spritesheet-sprites: @sprite-cities-arrow @sprite-cities-marker @sprite-search;
@spritesheet: 34px 28px '../img/sprite/png-sprite.png' @spritesheet-sprites;

/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}
*/
.sprite-width(@sprite) {
  width: extract(@sprite, 5);
}

.sprite-height(@sprite) {
  height: extract(@sprite, 6);
}

.sprite-position(@sprite) {
  @sprite-offset-x: extract(@sprite, 3);
  @sprite-offset-y: extract(@sprite, 4);
  background-position: @sprite-offset-x @sprite-offset-y;
}

.sprite-image(@sprite) {
  @sprite-image: extract(@sprite, 9);
  @sprite-image-bare: ~`"@{sprite-image}".slice(1, -1)`;
  background-image: url(@sprite-image-bare);
}

.sprite(@sprite) {
  .sprite-image(@sprite);
  .sprite-position(@sprite);
  .sprite-width(@sprite);
  .sprite-height(@sprite);
}

/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/
.sprites(@sprites, @i: 1) when (@i <= length(@sprites)) {
  @sprite: extract(@sprites, @i);
  @sprite-name: e(extract(@sprite, 10));
  .@{sprite-name} {
    .sprite(@sprite);
  }
  .sprites(@sprites, @i + 1);
}
